// Basic Styling Google Homepage

/* Stage */

#stage {
    height: 500px;
    width: 800px;
}


/* DO NOT EDIT BELOW THIS LINE */

input {
    background: url(data:image/png;base64,) no-repeat center right;
    border: 1px solid #aaa;
    display: block;
    padding: 1em;
    margin: 0 auto;
    outline: none;
    line-height: 36px;
    width: 50%;
}

div {
    margin: 3em auto;
    text-align: center;
    width: 50%;
}

button {
    display: inline-block;
    background-color: #f2f2f2;
    border: 1px solid #f2f2f2;
    border-radius: 2px;
    color: #757575;
    cursor: default;
    font-family: arial, sans-serif;
    font-size: 1em;
    font-weight: bold;
    padding: .5em 1em;
    text-align: center;
}